import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { createRouterScroller } from '@angular/router/src/router_module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { RoutesPageModule } from './routes/routes.module';
import { ChatsPageModule } from './chats/chats.module';
import { SettingsPageModule } from './settings/settings.module';
import { TabsPage } from './tabs.page';


const routes: Routes = [
  {
    path: 'tabs',
    component: TabsPage,
    children: [
      {
        path: 'routes',
        outlet: 'tab1',
        component: RoutesPageModule
      },
      {
        path: 'chats',
        outlet: 'tab2',
        component: ChatsPageModule
      },
      {
        path: 'settings',
        outlet: 'tab3',
        component: SettingsPageModule
      }
    ]
  },
  {
    path: '',
    redirectTo: '/tabs/(tab1:routes)',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    IonicModule,
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
    RoutesPageModule,
    ChatsPageModule,
    SettingsPageModule
  ],
  declarations: [TabsPage]
})
export class TabsPageModule { }
